<template>
<div class="top-title">
  <h1>Help Desk</h1>
  <h4>If you have any questions or suggestions for improvement, </h4>
  <h4>please don't hesitate to contact us.</h4>
</div>
<div class="submitForm">
  <form @submit.prevent="submitForm">
    <label for="email">Email:</label>
    <input type="email" id="email" v-model="email" required>
    <br>
    <label for="message">Message:</label>
    <textarea id="message" v-model="message" required></textarea>
    <br>
    <button type="submit" class="button down" >submit</button>
  </form>
  <br>
  <!-- <div v-if="submitForm">Email sent successfully!</div> -->
  <div v-if="errorMessage" class="align-c">{{ errorMessage }}</div>
</div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      form: {
        email: '',
        name: '',
        createdDate: ''
      },
      errorMessage: ''
    }
  },
  methods: {
    async submitForm () {
      try {
        this.errorMessage = 'Thank you for your message. We\'re processing it now...'
        const params = {
          email: this.form.email,
          message: this.form.message,
          createdDate: new Date().toISOString(), // .slice(0, 10),
          sheet_name: 'contact'
        }
        const response = await axios.post(
          'https://script.google.com/macros/s/AKfycbyfIAbqO2IU6HtHGUxn_c4VP-bN1vw9e65H6jKBt7Y6rBlQAw7anfJEKVlNSoNnXbA7/exec',
          null,
          {
            params
          }
        )
        if (response.data.result === 'error') {
          this.errorMessage = response.data.error
        } else {
          this.errorMessage = 'We have received your message and will get back to you soon.'
        }
      } catch (error) {
        this.errorMessage = 'Error saving data.'
      }
    }
  }
}
</script>
<style scoped>
 .top-title{
  padding-bottom: 70px;
 }
</style>
